.card {
  display: flex;
  flex-direction: column;
  padding: 15px 14px;
  width: $width-of-card;

  border: 1px solid $color-of-border;
  border-radius: 5px;

  transition: box-shadow 0.1s;

  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
    
  }

  &__img {
    margin: 0 auto 24px;
    padding: 12px 0 18px;
    width: 160px;
    height: 134px;
    box-sizing: content-box;

    border: 3px solid transparent;

    transition: transform 1s, border 1s;

    &:hover {
      border: 3px solid $color-of-border;
      
      transform: scale(1.2);
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 5px;
    @include font-big(12px, 15px, 500);
  }

  &__code {
    margin-bottom: 15px;
    @include font-normal;
  }

  &__about {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__reviews {
    @include font-normal;
  }

  &__stars {
    display: flex;
  }

  &__item {
    margin-right: 4px;
    @include size-of-star(15px);
    background: url(../images/star.svg) no-repeat center 50%;
  }

  &__stars:last-child {
    margin-right: 0;
  }

  &__stars--1 &__item:nth-child(-n+1),
  &__stars--2 &__item:nth-child(-n+2),
  &__stars--3 &__item:nth-child(-n+3),
  &__stars--4 &__item:nth-child(-n+4),
  &__stars--5 &__item:nth-child(-n+5) {
    background-image: url(../images/star-active.svg);
  }

  &__info {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 20px;
    @include font-normal;
  }

  &__info-price {
    @include font-big(16px, 19px, bold);
  }

  &__btn-buy {
    display: block;
    width: 100%;
    height: 40px;
  
    font-size: 14px;
    line-height: 40px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    background-color: $color-of-item;
    border-radius: 5px;
    border: 1px solid $color-of-item;

    transition: background-color 1s, color 1s;

    &:hover {
      color: $color-of-item;
      background: $color-of-background;
    }
  }
}
